/* styles.css */
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

body {
  color: var(--gray-600, #4B5563);
  padding: 50px 50px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
}

nav {
  border-bottom: 1px solid rgba(255, 255, 255, 0.00);
  background: var(--gray-default, #FFF);;
}

.container {
  display: flex;
  padding: 0px 32px;
  justify-content: space-between;
  align-items: center;
  flex: 1 0 0;
}

.intro-body {
  color: var(--gray-600, #4B5563);
  /* Body2/Normal - All */
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  width: 750px;
}

.big-header {
  padding: 30px 0px;
  color: #000;
  font-family: Inter;
  font-size: 128px;
  font-style: normal;
  font-weight: 700;
  line-height: 92px; /* 71.875% */
  letter-spacing: -2.56px;
  width: 435px;
  height: 183px;
  flex-shrink: 0;
}
.small-header {
  width: 100px;
  height: 70px;
  flex-shrink: 0;
  color: #000;
  font-family: Inter;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 35px; /* 72.917% */
}
.page {
  padding: 30px 40px;
}

.skills-box {
  padding: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}

.tech {
  display: flex;
  height: 97px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.tech-text {
  color: var(--gray-600, #4B5563);
  /* Body1/Normal - Dektop */
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px; /* 155.556% */
}

.section {
  padding: 100px 0px;
}
.small-section {
  padding: 15px 0px;
}

.wide-rectangle {
  margin-bottom: 35px;
  padding:15px 20px;
  width: 1173px;
  height: 286px;
  flex-shrink: 0;
  border-radius: 30px;
  border: 1px solid #000;
  background: #FFF;
}

.job-header {
  width: 214px;
  height: 49px;
  flex-shrink: 0;
  color: #000;
  font-family: Inter;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px; /* 88.889% */
}

.job-title {
  width: 229px;
  height: 70px;
  flex-shrink: 0;
  color: #000;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px; /* 133.333% */
}

.job-body {
  padding: 5px 0px ;
  color: #000;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 25px; /* 156.25% */
}


.left-column {
  flex-grow: 2;
  padding-right: 10px;
}

.container {
  display: flex; 
  flex-direction: row;
}

.jobs {
  padding: 50px 65px;
}
.others {
  display: flex;
}

.box {
  margin: auto;
  width: 478px;
  height: 272px;
}

.learn-more {
  color: #000;
font-family: Inter;
font-size: 16px;
font-style: normal;
font-weight: 700;
line-height: 32px; /* 200% */
text-decoration: none;
}


.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}
.intro-spacing {
  display: flex;
  padding-right: 10px;
}


/* Create two equal columns that sits next to each other */
.column {
  flex: 50%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
}